.background{
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -2;
    // object-fit: contain;
}

.background-blur{
    position: fixed;
    top: 0;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(5px);
}

.background-conteiner{
    margin: 0;
    padding: 0;    
    display: flex;
    flex-direction: column;
    width: 99vw;
}

